.page-header {
  position: fixed;
  width: 100%;
  background: #fff;
  z-index: 7;
  top: 0;
  transition: background 0.4s 0.25s ease;
  padding-bottom: 15px;
}
.page-header .container {
  z-index: 15;
}
.page-header:after {
  content: "";
  width: 100%;
  height: 1px;
  background: #f2efe2;
  position: absolute;
  left: 0;
  top: 120px;
  transition: all 0.25s ease;
}
.page-header.fixed {
  background: #f1eddf;
  z-index: 10;
  padding-bottom: 5px;
}
.page-header.fixed:after {
  top: 100%;
}
.page-header.fixed .top-logo {
  height: 88px;
  margin-bottom: 4px;
}
.page-header.fixed .top-nav__list {
  margin-top: 32px;
}
.page-header.fixed .user-links {
  margin-top: 20px;
}
.page-header.fixed .user-links__info {
  margin-bottom: 0px;
  font-size: 14px;
}
.page-header.fixed a.user-links__exit {
  font-size: 14px;
}
.page-header.fixed .user-course .user-links__icon {
  background: url(../img/svg/course-h.svg) no-repeat;
  background-size: cover;
}
.page-header.fixed .user-enter .user-links__icon {
  background: url(../img/svg/enter-h.svg) no-repeat;
  background-size: cover;
}
.page-header__wrap {
  display: flex;
  align-items: center;
}
.page-header__view {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1;
  margin: -15px 0 0 0;
}
.page-header .user-watch__list {
  padding-top: 10px;
  font-size: 14px;
}

.top-logo {
  display: inline-block;
  width: 130px;
  min-width: 130px;
  height: 156px;
  background: url(../img/content/top-logo.png) center no-repeat;
  background-size: contain;
  transition: all 0.25s ease;
  position: relative;
  z-index: 3;
}

.user-links {
  transition: margin 0.25s ease;
  position: relative;
  z-index: 3;
  font-size: 14px;
}
.user-links a {
  display: inline-block;
  vertical-align: middle;
  color: #011119;
  margin-right: -4px;
  transition: all 0.25s ease;
}
.user-links a:last-child {
  padding-right: 0;
  border-right: 0;
  margin-right: 2px;
}
.user-links a.user-enter {
  margin-left: 52px;
}
.user-links a:hover {
  color: #011119;
}
.user-links .user-enter {
  margin-left: 52px;
}
.user-links .user-enter a {
  text-decoration: none;
}
.user-links__icon {
  width: 33px;
  height: 33px;
  display: block;
  margin: 0 auto 5px;
  position: relative;
}
.user-links__icon img {
  max-width: 100%;
  height: auto;
  max-height: 100%;
}
.user-links__authorized:after {
  content: "";
  display: table;
  clear: both;
}
.user-links__info {
  display: block;
  margin-bottom: 5px;
  transition: all 0.25s ease;
}

a.user-links__exit {
  font-size: 14px;
  color: #348192;
  float: right;
  padding: 0;
  transition: all 0.25s ease;
}

.user-enter {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  transition: opacity 0.25s ease;
}
.user-enter .user-links__icon {
  background: url(../img/svg/enter-h.svg) no-repeat;
  background-size: cover;
  transition: background 0.25s ease;
}

.user-enter--authorized .top-submenu {
  width: 205px;
}
.user-enter--authorized .top-submenu:before {
  left: auto;
  right: 57px;
}
.user-enter--authorized:hover .top-submenu {
  opacity: 1;
  -webkit-transform: translate(0, 0);
  -ms-transform: translate(0, 0);
  transform: translate(0, 0);
  z-index: 2;
  visibility: visible;
  -webkit-transition: all 0.25s ease-in-out;
  transition: all 0.25s ease-in-out;
}
.user-enter--authorized .top-submenu__link {
  display: block;
  margin-bottom: 15px !important;
  color: #000;
  padding: 0 20px;
  border-left: 2px solid transparent;
  text-decoration: none;
  font-size: 18px;
}
.user-enter--authorized .top-submenu__link:hover {
  color: #011119;
}

.user-course {
  display: inline-block;
  text-decoration: none;
}
.user-course .user-links__icon {
  background: url(../img/svg/course-h.svg) no-repeat;
  background-size: cover;
  transition: background 0.25s ease;
}
.user-course__link {
  text-decoration: none;
}
.user-course__count {
  border-radius: 50%;
  background: #ec1c24;
  color: #fff;
  font-size: 14px;
  display: inline-block;
  /* padding: 0px 5px; */
  height: 16px;
  line-height: 15px;
  min-width: 16px;
  text-align: center;
  position: absolute;
  right: -8px;
  top: -4px;
}
.user-course__list {
  position: absolute;
  background: #fff;
  border-radius: 4px;
  width: 512px;
  right: 0;
  margin-top: 20px;
  box-shadow: 0px 2px 14px 0px rgba(0, 0, 0, 0.31);
  display: none;
  z-index: 1;
}
.user-course__list:after {
  content: "";
  display: block;
  bottom: 100%;
  height: 20px;
  width: 100%;
  background: transparent;
  position: absolute;
  left: 0;
}
.user-course__list:before {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 0 9px 8px 9px;
  border-color: transparent transparent #ffffff transparent;
  right: 102px;
  bottom: 100%;
  position: absolute;
}
.user-course__close {
  width: 16px;
  height: 16px;
  display: inline-block;
  position: absolute;
  right: 18px;
  top: 18px;
  background: url(../img/svg/close-course.svg) no-repeat;
  background-size: contain;
  z-index: 4;
  margin: 0 !important;
}
.user-course__roll {
  border-bottom: 2px solid #f2efe2;
  font-size: 18px;
  display: flex;
  position: relative;
  padding-bottom: 83px;
}
.user-course__roll ul {
  list-style-type: none;
  margin: 0;
  padding: 20px 32px 20px 0;
}
.user-course__roll ul li {
  list-style-type: none;
  margin-bottom: 20px;
}
.user-course__roll ul li:last-child {
  margin-bottom: 0;
}
.user-course__roll ol {
  margin: 0;
  padding: 20px 13px 20px 15px;
  counter-reset: myCounter;
  position: relative;
}
.user-course__roll ol:before {
  content: "";
  display: inline-block;
  height: 100%;
  width: 62px;
  left: 0;
  top: 0;
  position: absolute;
  background: #f2efe2;
}
.user-course__roll ol li {
  list-style-type: none;
  padding-left: 68px;
  position: relative;
  margin-bottom: 20px;
  line-height: 1.3;
}
.user-course__roll ol li:last-child {
  margin-bottom: 0;
}
.user-course__roll ol li:before {
  counter-increment: myCounter;
  content: counter(myCounter);
  position: absolute;
  left: 1px;
  top: 50%;
  margin-top: -15px;
  color: #385443;
  font-size: 18px;
  border-radius: 50%;
  padding: 1px;
  width: 30px;
  height: 30px;
  line-height: 25px;
  text-align: center;
  background: transparent;
  border: 1px solid #385443;
  font-family: "din-round-pro-bold", sans-serif;
}
.user-course__roll ol li.active:before {
  background: #fff;
  width: 34px;
  height: 34px;
  line-height: 30px;
  border: 1px solid #fff;
  left: -1px;
}
.user-course__roll ol li.active:after {
  content: "";
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 7px 8px 7px 0;
  position: absolute;
  /* right: 100%; */
  left: 39px;
  top: 50%;
  margin-top: -5px;
  border-color: transparent #ffffff transparent transparent;
}
.user-course__roll a {
  color: #000 !important;
  text-decoration: none;
  display: block;
}
.user-course__roll a.course-visited, .user-course__roll a:hover {
  color: #9ea2a3 !important;
}
.user-course__roll a.course-visited {
  position: relative;
}
.user-course__roll a.course-visited:before {
  content: "";
  display: inline-block;
  width: 10px;
  height: 7px;
  background: url(../img/svg/tick.svg) no-repeat;
  position: absolute;
  left: -14px;
  top: 10px;
}
.user-course__change {
  padding-left: 24px;
  max-height: 400px;
  overflow-y: auto;
}

.user-course-back {
  font-size: 18px;
  text-transform: uppercase;
  font-family: "din-round-pro-bold", sans-serif;
  border-bottom: 2px solid #f2efe2;
  padding: 15px 170px 15px 62px;
  position: relative;
  color: #011119;
  text-transform: uppercase;
  display: block;
  line-height: 1.4;
}
.user-course-back a {
  color: #011119 !important;
  margin-right: 0 !important;
  text-decoration: none;
}
.user-course-back__link {
  width: 10px;
  height: 17px;
  display: inline-block;
  position: absolute;
  left: 26px;
  top: 19px;
  background: url(../img/svg/back.svg) no-repeat;
  background-size: contain;
}

.user-course-info {
  padding: 20px 35px 20px 69px;
  background: #fff;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 2px solid #f2efe2;
