.workarea input[type=text]:focus,
.workarea input[type=email]:focus,
.workarea input[type=search]:focus,
.workarea input[type=password]:focus, .popup__content input[type=text]:focus,
.popup__content input[type=email]:focus,
.popup__content input[type=search]:focus,
