.workarea input[type=checkbox]:not(:checked),
.workarea input[type=checkbox]:checked, .popup__content input[type=checkbox]:not(:checked),
.popup__content input[type=checkbox]:checked {
  position: absolute;
  left: -9999px;
}
.workarea input[type=checkbox]:not(:checked) + label,
.workarea input[type=checkbox]:checked + label, .popup__content input[type=checkbox]:not(:checked) + label,
.popup__content input[type=checkbox]:checked + label {
  position: relative;
  cursor: pointer;
  padding-left: 30px;
  line-height: 22px;
  display: inline-block;
  font-size: 16px;
}
.workarea input[type=checkbox]:not(:checked) + label:before,
.workarea input[type=checkbox]:checked + label:before, .popup__content input[type=checkbox]:not(:checked) + label:before,
.popup__content input[type=checkbox]:checked + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 22px;
  height: 22px;
  border: 1px solid #9ea2a3;
  border-radius: 3px;
  cursor: pointer;
  z-index: 5;
}
.workarea input[type=checkbox]:checked + label:before, .popup__content input[type=checkbox]:checked + label:before {
  background: #fff;
