.workarea input[type=checkbox]:not(:checked) + label:after,
.workarea input[type=checkbox]:checked + label:after, .popup__content input[type=checkbox]:not(:checked) + label:after,
.popup__content input[type=checkbox]:checked + label:after {
  content: "";
  background: url("../img/svg/checked.svg") center no-repeat;
  width: 17px;
  height: 13px;
  display: inline-block;
  position: absolute;
  top: 4px;
  left: 4px;
  z-index: 10;
