.workarea input[type=radio]:checked + label:before, .popup__content input[type=radio]:not(:checked) + label:before,
.popup__content input[type=radio]:checked + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 20px;
