.workarea input[type=radio]:not(:checked) + label:after,
.workarea input[type=radio]:checked + label:after, .popup__content input[type=radio]:not(:checked) + label:after,
.popup__content input[type=radio]:checked + label:after {
  content: "";
  background: #385543;
